.brand {
  @include devices(tablet) {
    &__content {
      padding: 60px 0;
      display: flex;
    }

    &__title {
      display: flex;
      flex-direction: column;
    }

    &__heading {
      font-size: $fs-700;
      margin-bottom: 35px;
      width: 232px;
    }

    &__line {
      border: 1px solid $clr-arrow;
      margin-bottom: 35px;
    }

    &__arrows {
      @include flexCenter;
      gap: 15px;
    }

    &__arrow {
      @include flexCenter;
      @include styleArrow;
    }

    &__icon {
      @include styleArrowIcon;
      color: $clr-primary-purple;
    }

    &__cards {
      position: relative;
    }

    &__card {
      width: 435px;
      @include flexCenterColumn;
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      margin-bottom: 42px;
      border-radius: 10px;
    }

    &__img {
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }

    &__releaseDate {
      display: flex;
      left: -20px;
      top: 255px;
      position: absolute;
    }

    &__release {
      @include styleReleaseDate;
      background-color: $clr-primary-purple;
      color: $clr-white;
    }

    &__date {
      @include styleReleaseDate;
      margin-left: -5px;
      background-color: $clr-white;
      color: $clr-medium-gray;
      border: 1px solid $clr-primary-purple;
    }

    &__text {
      padding: 40px 40px;
      font-size: $fs-450;
      color: $clr-pink;
    }

    &__underCard {
      @include flexCenter;
      gap: 22px;
    }

    &__buttonLine {
      width: 180px;
      height: 1px;
      border: 1px solid $clr-arrow;
    }
  }

  @include devices(mobile) {
    &__content {
      padding: 40px 0;
      @include flexCenterColumn;
    }

    &__title {
      width: 274px;
      margin-bottom: 35px;
    }

    &__heading {
      margin-bottom: 35px;
    }

    &__line {
      border: 1px solid $clr-arrow;
      margin-bottom: 35px;
    }

    &__arrows {
      @include flexCenter;
      gap: 15px;
    }

    &__arrow {
      @include flexCenter;
      @include styleArrow;
    }

    &__icon {
      @include styleArrowIcon;
      color: $clr-primary-purple;
    }

    &__cards {
      position: relative;
    }

    &__card {
      width: 274px;
      @include flexCenterColumn;
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      margin-bottom: 42px;
      border-radius: 10px;
    }

    &__img {
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }

    &__releaseDate {
      display: flex;
      left: -20px;
      top: 255px;
      position: absolute;
    }

    &__release {
      @include styleReleaseDate;
      background-color: $clr-primary-purple;
      color: $clr-white;
    }

    &__date {
      @include styleReleaseDate;
      margin-left: -5px;
      background-color: $clr-white;
      color: $clr-medium-gray;
      border: 1px solid $clr-primary-purple;
    }

    &__text {
      padding: 25px 25px;
      font-size: $fs-350;
      color: $clr-pink;
    }

    &__underCard {
      @include flexCenter;
      gap: 0;
    }

    &__buttonLine {
      width: 42px;
      height: 1px;
      border: 1px solid $clr-arrow;
    }
  }
}
