// Rectangle style
.rectangle {
  @include devices(large-tablet) {
    display: none;
  }
  @include devices(mobile) {
    display: none;
  }
}
// Header style
.header {
  // Tablet queries
  @include devices(tablet) {
    position: relative;
    padding: 16px 0;

    &__content {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__nav {
      display: none;
      position: absolute;
      top: 85px;
      left: 0;
      width: 100%;
      padding: 20px;
      background-color: $clr-white;
    }

    &__list {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 40px;
    }

    &__link {
    }

    &__number {
      display: block;
      margin-left: auto;
    }

    &__line {
      display: block;
      border: 1px solid $clr-black;
      height: 15px;
      margin: 0 10px;
    }

    &__icon {
      width: 25px;
      height: 25px;
      cursor: pointer;
    }

    &__icon--close {
      display: none;
    }

    &__icon--open {
      display: block;
    }
    .active {
      display: block;
    }
    // Mobile queries
    @include devices(mobile) {
      position: relative;
      padding: 16px 0;

      &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      &__nav {
        display: none;
        position: absolute;
        top: 85px;
        left: 0;
        width: 100%;
        padding: 20px;
        background-color: $clr-white;
      }

      &__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 40px;
      }

      &__link {
      }

      &__number {
        display: none;
      }

      &__line {
        display: none;
      }

      &__icon {
        width: 25px;
        height: 25px;
        cursor: pointer;
      }

      &__icon--close {
        display: none;
      }

      &__icon--open {
        display: block;
      }
      .active {
        display: block;
      }
    }
  }
}

